import React from 'react';
import {Dialog, DialogTitle, DialogContent} from '@mui/material';

function Changes(props) {

    const open = props.open;
    const setOpen = props.setOpen;

    return (
        <div className="dialog">
            <Dialog aria-label='dialog-title' open={open} onClose={() => setOpen(false)} maxWidth="lg" fullWidth>
                <DialogTitle className='dialog-container' id='dialog-title'>
                <div style={{display: "flex", justifyContent: "space-between", alignItems: "center"}}>
                    <div>Changes</div>
                    <button onClick={() => setOpen(false)} className='calc-btn' style={{width: "37px", display: "flex", backgroundColor: "red", textAlign:"center", alignItems: "center", justifyContent: "center", margin: "0"}}>&#10005;</button>
                </div>
                </DialogTitle>
                <DialogContent className='dialog-content'>
                    <div className='date'>30/8/2023</div>
                    {/* <div className='changes'> - Added Kafka </div> */}
                    <div className='changes'> - Added Welt Yang</div>
                    <div className='changes'> - Relics/Ornaments buffs that don't have a condition are now automatically added</div>
                    <div className='date'>29/8/2023</div>
                    <div className='changes'> - Fixed a bug where if you switch a relic or an ornament that has stacks the buff wouldn't be cleared properly</div>
                    <div className='changes'> - Fixed a bug with Fleetless and Broken Keel, which made them not stackable</div>
                    <div className='changes'> - Added Sampo</div>
                    <div className='changes'> - Fixed Pela's traces</div>
                    <div className='date'>28/8/2023</div>
                    <div className='changes'> - Added Pan-Galagtic planar ornament</div>
                    <div className='changes'> - Added Qingque</div>
                    <div className='changes'> - Added Jing Yuan</div>
                    <div className='date'>27/8/2023</div>
                    <div className='changes'> - Added Himeko</div>
                    <div className='changes'> - Added Serval</div>
                    <div className='changes'> - Added Herta</div>
                    <div className='changes'> - Added Yanqing</div>
                    <div className='date'>26/8/2023</div>
                    <div className='changes'> - Added Hook</div>
                    <div className='changes'> - Added Clara</div>
                    <div className='changes'> - Added Blade</div>
                    <div className='changes'> - Added Arlan</div>
                </DialogContent>
            </Dialog>
        </div>
    )
}

export default Changes;