import React from 'react';
import { globalData } from '../../globalData';
import { useContext } from 'react';
import {Dialog, DialogTitle, DialogContent} from '@mui/material';

function Skills(props) {

    const { character, basicAttackLevel, skillLevel, ultimateLevel, talentLevel, eidolon,
        setBasicAttackLevel, setSkillLevel, setUltimateLevel, setTalentLevel, setEidolon, level } = useContext(globalData);

    const open = props.open;
    const setOpen = props.setOpen;
    
    let enhancedBasicAttack1 = "";
    try {
        enhancedBasicAttack1 = (
            <>
                <div className='attack-container mt-4'>
                    <h4>{character.basic1Description(basicAttackLevel).name}</h4>
                </div>
                <div>{character.basic1Description(basicAttackLevel).description}</div>
            </>
        )
    } catch (error) {
        
    }

    let enhancedBasicAttack2 = "";
    try {
        enhancedBasicAttack2 = (
            <>
                <div className='attack-container mt-4'>
                    <h4>{character.basic2Description(basicAttackLevel).name}</h4>
                </div>
                <div>{character.basic2Description(basicAttackLevel).description}</div>
            </>
        )
    } catch (error) {
        
    }

    let enhancedBasicAttack3 = "";
    try {
        enhancedBasicAttack3 = (
            <>
                <div className='attack-container mt-4'>
                    <h4>{character.basic3Description(basicAttackLevel).name}</h4>
                </div>
                <div>{character.basic3Description(basicAttackLevel).description}</div>
            </>
        )
    } catch (error) {
        
    }

    return (
        <div className="dialog">
            <Dialog aria-label='dialog-title' open={open} onClose={() => setOpen(false)} maxWidth="lg" fullWidth>
                <DialogTitle className='dialog-container' id='dialog-title'>
                <div style={{display: "flex", justifyContent: "space-between", alignItems: "center"}}>
                        <div>Skills and Eidolons</div>
                        <button onClick={() => setOpen(false)} className='calc-btn' style={{width: "37px", display: "flex", backgroundColor: "red", textAlign:"center", alignItems: "center", justifyContent: "center", margin: "0"}}>&#10005;</button>
                </div>
                </DialogTitle>
                <DialogContent className='dialog-content'>
                    <div className='character-skills-container'>
                        <div className='skills'>
                            <div className='header centered'>Skills</div>
                            <div className='attack-container'>
                                <h4>{character.basicDescription(basicAttackLevel).name} (Basic Attack)</h4>
                                <div style={{display: "flex"}}>
                                    <div className='header'>Level</div>
                                    <select className='levels header buff-input' style={{width: "70px", height: "35px"}} value={basicAttackLevel} onChange={(e) => setBasicAttackLevel(e.target.value)}>
                                        <option value={0}>1</option>
                                        <option value={1}>2</option>
                                        <option value={2}>3</option>
                                        <option value={3}>4</option>
                                        <option value={4}>5</option>
                                        <option value={5}>6</option>
                                        <option value={6}>7</option>
                                    </select>
                                </div>
                            </div>
                            <div>{character.basicDescription(basicAttackLevel).description}</div>
                            
                            {enhancedBasicAttack1}
                            {enhancedBasicAttack2}
                            {enhancedBasicAttack3}

                            <div className='attack-container mt-4'>
                                <h4>{character.skillDescription(skillLevel).name} (Skill)</h4>
                                <div style={{display: "flex"}}>
                                    <div className='header'>Level</div>
                                    <select className='levels header buff-input' style={{width: "70px", height: "35px"}} value={skillLevel} onChange={(e) => setSkillLevel(e.target.value)}>
                                        <option value={0}>1</option>
                                        <option value={1}>2</option>
                                        <option value={2}>3</option>
                                        <option value={3}>4</option>
                                        <option value={4}>5</option>
                                        <option value={5}>6</option>
                                        <option value={6}>7</option>
                                        <option value={7}>8</option>
                                        <option value={8}>9</option>
                                        <option value={9}>10</option>
                                        <option value={10}>11</option>
                                        <option value={11}>12</option>
                                    </select>
                                </div>
                            </div>
                            <div>{character.skillDescription(skillLevel).description}</div>

                            <div className='attack-container mt-4'>
                                <h4>{character.ultimateDescription(ultimateLevel).name} (Ultimate)</h4>
                                <div style={{display: "flex"}}>
                                    <div className='header'>Level</div>
                                    <select className='levels header buff-input' style={{width: "70px", height: "35px"}} value={ultimateLevel} onChange={(e) => setUltimateLevel(e.target.value)}>
                                        <option value={0}>1</option>
                                        <option value={1}>2</option>
                                        <option value={2}>3</option>
                                        <option value={3}>4</option>
                                        <option value={4}>5</option>
                                        <option value={5}>6</option>
                                        <option value={6}>7</option>
                                        <option value={7}>8</option>
                                        <option value={8}>9</option>
                                        <option value={9}>10</option>
                                        <option value={10}>11</option>
                                        <option value={11}>12</option>
                                    </select>
                                </div>
                            </div>
                            <div>{character.ultimateDescription(ultimateLevel).description}</div>

                            <div className='attack-container mt-4'>
                                <h4>{character.talentDescription(talentLevel).name} (Talent)</h4>
                                <div style={{display: "flex"}}>
                                    <div className='header'>Level</div>
                                    <select className='levels header buff-input' style={{width: "70px", height: "35px"}} value={talentLevel} onChange={(e) => setTalentLevel(e.target.value)}>
                                        <option value={0}>1</option>
                                        <option value={1}>2</option>
                                        <option value={2}>3</option>
                                        <option value={3}>4</option>
                                        <option value={4}>5</option>
                                        <option value={5}>6</option>
                                        <option value={6}>7</option>
                                        <option value={7}>8</option>
                                        <option value={8}>9</option>
                                        <option value={9}>10</option>
                                        <option value={10}>11</option>
                                        <option value={11}>12</option>
                                    </select>
                                </div>
                            </div>
                            <div>{character.talentDescription(talentLevel).description}</div>

                            <div className='attack-container mt-4'>
                            <h4>{character.techniqueDescription().name} (Technique)</h4>
                            </div>
                            <div>{character.techniqueDescription().description}</div>


                            <div className='eidolons'>
                                <div style={{display: "flex", justifyContent: "center", alignItems: "center"}} className='mt-5'>
                                    <div className='header centered'>Traces</div>
                                </div>
                                {level >= 3 ?
                                    <>
                                        <div className='attack-container'>
                                            <h4>{character.A2().name} (A2)</h4>
                                        </div>
                                        <div>{character.A2().description}</div>
                                    </>
                                    :
                                    <>
                                        <div className='attack-container locked'>
                                            <h4>{character.A2().name} (A2)</h4>
                                        </div>
                                        <div className='locked'>{character.A2().description}</div>
                                        <div className='required'>Requires Ascension 2 (Level 30+)</div>
                                    </>
                                }

                                {level >= 6 ?
                                    <>
                                        <div className='attack-container mt-4'>
                                            <h4>{character.A4().name} (A4)</h4>
                                        </div>
                                        <div>{character.A4().description}</div>
                                    </>
                                    :
                                    <>
                                        <div className='attack-container mt-4 locked'>
                                            <h4>{character.A4().name} (A4)</h4>
                                        </div>
                                        <div className='locked'>{character.A4().description}</div>
                                        <div className='required'>Requires Ascension 4 (Level 50+)</div>
                                    </>
                                }

                                {level >= 6 ?
                                    <>
                                        <div className='attack-container mt-4'>
                                            <h4>{character.A6().name} (A6)</h4>
                                        </div>
                                        <div>{character.A6().description}</div>
                                    </>
                                :
                                    <>
                                        <div className='attack-container mt-4 locked'>
                                            <h4>{character.A6().name} (A6)</h4>
                                        </div>
                                        <div className='locked'>{character.A6().description}</div>
                                        <div className='required'>Requires Ascension 6 (Level 70+)</div>
                                    </>
                                }
                                {/* <div className='mt-5'>
                                    <div className='header muted-text'>Note</div>
                                </div>
                                <div className='muted-text'> Traces are baked into the character levels, which means A2, A4, and A6 are unlocked at levels 30, 60, and 80 respectively, as well as any minor traces related to any certain level.</div> */}
                            </div>

                            <div className='eidolons'>
                                <div style={{display: "flex", justifyContent: "center", alignItems: "center"}} className='mt-5'>
                                    <div className='header centered'>Eidolons</div>
                                    <select className='header buff-input' style={{width: "70px", height: "35px"}} value={eidolon} onChange={(e) => setEidolon(e.target.value)}>
                                        <option value={0}>0</option>
                                        <option value={1}>1</option>
                                        <option value={2}>2</option>
                                        <option value={3}>3</option>
                                        <option value={4}>4</option>
                                        <option value={5}>5</option>
                                        <option value={6}>6</option>
                                    </select>
                                </div>
                                {eidolon >= 1 ?
                                    <>
                                        <div className='attack-container'>
                                            <h4>{character.E1().name} (E1)</h4>
                                        </div>
                                        <div>{character.E1().description}</div>
                                    </>
                                    :
                                    <>
                                        <div className='attack-container locked'>
                                            <h4>{character.E1().name} (E1)</h4>
                                        </div>
                                        <div className='locked'>{character.E1().description}</div>
                                        <div className='required'>Requires 1 Eidolon</div>
                                    </>
                                }
                                
                                {eidolon >= 2 ?
                                    <>
                                        <div className='attack-container mt-4'>
                                            <h4>{character.E2().name} (E2)</h4>
                                        </div>
                                        <div>{character.E2().description}</div>
                                    </>
                                    :
                                    <>
                                        <div className='attack-container mt-4 locked'>
                                            <h4>{character.E2().name} (E2)</h4>
                                        </div>
                                        <div className='locked'>{character.E2().description}</div>
                                        <div className='required'>Requires 2 Eidolons</div>
                                    </>
                                }

                                {eidolon >= 3 ?
                                    <>
                                        <div className='attack-container mt-4'>
                                            <h4>{character.E3().name} (E3)</h4>
                                        </div>
                                        <div>{character.E3().description}</div>
                                    </>
                                    :
                                    <>
                                        <div className='attack-container mt-4 locked'>
                                            <h4>{character.E3().name} (E3)</h4>
                                        </div>
                                        <div className='locked'>{character.E3().description}</div>
                                        <div className='required'>Requires 3 Eidolons</div>
                                    </>
                                }

                                {eidolon >= 4 ?
                                    <>
                                        <div className='attack-container mt-4'>
                                            <h4>{character.E4().name} (E4)</h4>
                                        </div>
                                        <div>{character.E4().description}</div>
                                    </>
                                    :
                                    <>
                                        <div className='attack-container mt-4 locked'>
                                            <h4>{character.E4().name} (E4)</h4>
                                        </div>
                                        <div className='locked'>{character.E4().description}</div>
                                        <div className='required'>Requires 4 Eidolons</div>
                                    </>
                                }

                                {eidolon >= 5 ?
                                    <>
                                        <div className='attack-container mt-4'>
                                            <h4>{character.E5().name} (E5)</h4>
                                        </div>
                                        <div>{character.E5().description}</div>
                                    </>
                                    :
                                    <>
                                        <div className='attack-container mt-4 locked'>
                                            <h4>{character.E5().name} (E5)</h4>
                                        </div>
                                        <div className='locked'>{character.E5().description}</div>
                                        <div className='required'>Requires 5 Eidolons</div>
                                    </>
                                }

                                {eidolon >= 6 ?
                                    <>
                                        <div className='attack-container mt-4'>
                                            <h4>{character.E6().name} (E6)</h4>
                                        </div>
                                        <div>{character.E6().description}</div>
                                    </>
                                    :
                                    <>
                                        <div className='attack-container mt-4 locked'>
                                            <h4>{character.E6().name} (E6)</h4>
                                        </div>
                                        <div className='locked'>{character.E6().description}</div>
                                        <div className='required'>Requires 6 Eidolons</div>
                                    </>
                                }
                            </div>
                        </div>
                    </div>
                </DialogContent>
            </Dialog>
        </div>
    )
}

export default Skills;