import React, { useContext, useState, useEffect } from 'react';
import { globalData } from '../../globalData';
import starImage from '../../../assets/star.webp';


function ConeDetails() {
    const {
        lightCone,
        imposition,
        setConeRank,
        setImposition,
        isChecked,
    } = useContext(globalData);

    const [selectedLevelValue, setSelectedLevelValue] = useState("1");
    const [selectedRankValue, setSelectedRankValue] = useState("1");

    let content = "";

    useEffect(() => {
        setSelectedLevelValue("1");
        setSelectedRankValue("1");
    }, [isChecked]);

    const handleSelectChange = (event) => {
        const selectedIndex = event.target.selectedIndex;
        setSelectedLevelValue(event.target.value);
        setConeRank(selectedIndex);
    };

    const handleSelectImposition = (event) => {
        const selectedIndex = event.target.selectedIndex;
        setSelectedRankValue(event.target.value);
        setImposition(selectedIndex);
    }

    let levelDropdown = (
        <div className='rank'>
            <div>Level</div>
            <select name="level" id="level" className='levels' onChange={handleSelectChange} value={selectedLevelValue}>
                <option value="1">1</option>
                <option value="20">20</option>
                <option value="20+">20+</option>
                <option value="40">40</option>
                <option value="40+">40+</option>
                <option value="50">50</option>
                <option value="50+">50+</option>
                <option value="60">60</option>
                <option value="60+">60+</option>
                <option value="70">70</option>
                <option value="70+">70+</option>
                <option value="80">80</option>
            </select>
        </div>
    );

    let impositionDropDown = (
        <div className='rank'>
            <div>Imposition</div>
            <select name="imp" id="imp" className='levels' onChange={handleSelectImposition} value={selectedRankValue}>
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
                <option value="5">5</option>
            </select>
        </div>
    );

    if(lightCone){
        if(lightCone.rarity === "fiveStar") {
            content = (
                <div className='cone-details centered'>
                    <div>
                        <img src={starImage} width={25} alt=""></img>
                        <img src={starImage} width={25} alt=""></img>
                        <img src={starImage} width={25} alt=""></img>
                        <img src={starImage} width={25} alt=""></img>
                        <img src={starImage} width={25} alt=""></img>
                    </div>
                    <div className='lightcone-5-text' style={{textAlign: "center"}}>{lightCone.name}</div>
                    <div className='cone-levels'>
                        {levelDropdown}
                        {impositionDropDown}
                    </div>
                    <div className='mt-3'>Light Cone Effect</div>
                    <div>{lightCone.weaponDescription(imposition)}</div>
                </div>
            )
        } else if(lightCone.rarity === "fourStar"){
            content = (
                <div className='cone-details centered'>
                    <div className='lightcone-4-text' style={{textAlign: "center"}}>{lightCone.name}</div>
                    <div>
                        <img src={starImage} width={25} alt=""></img>
                        <img src={starImage} width={25} alt=""></img>
                        <img src={starImage} width={25} alt=""></img>
                        <img src={starImage} width={25} alt=""></img>
                    </div>
                    <div className='cone-levels'>
                        {levelDropdown}
                        {impositionDropDown}
                    </div>
                    <div className='mt-3'>Light Cone Effect</div>
                    <div>{lightCone.weaponDescription(imposition)}</div>
                </div>
            )
        } else {
            content = (
                <div className='cone-details centered'>
                    <div className='lightcone-3-text' style={{textAlign: "center"}}>{lightCone.name}</div>
                    <div>
                        <img src={starImage} width={25} alt=""></img>
                        <img src={starImage} width={25} alt=""></img>
                        <img src={starImage} width={25} alt=""></img>
                    </div>
                    <div className='cone-levels'>
                        {levelDropdown}
                        {impositionDropDown}
                    </div>
                    <div className='mt-3'>Light Cone Effect</div>
                    <div>{lightCone.weaponDescription(imposition)}</div>
                </div>
            )
        }
    }

    return (
        content
    )
}


export default ConeDetails;