import React from 'react';
import {Dialog, DialogTitle, DialogContent} from '@mui/material';

function Intro(props) {

    const open = props.open;
    const setOpen = props.setOpen;

    return (
        <div className="dialog">
            <Dialog aria-label='dialog-title' open={open} onClose={() => setOpen(false)} maxWidth="lg" fullWidth>
                <DialogTitle className='dialog-container' id='dialog-title'> 
                <div style={{display: "flex", justifyContent: "space-between", alignItems: "center"}}>
                    <div>Introductions</div>
                    <button onClick={() => setOpen(false)} className='calc-btn' style={{width: "37px", display: "flex", backgroundColor: "red", textAlign:"center", alignItems: "center", justifyContent: "center", margin: "0"}}>&#10005;</button>
                </div>
                </DialogTitle>
                <DialogContent className='dialog-content'>
                    <div className='date'>About me</div>
                    <div className='changes'>- Hi, I am Zeeka, the creator of this calculator, I made this app in an attempt to learn the React Framework better and to test different builds in star rail without needing to log into the game each time to try something new.</div>
                    <div className='changes'>- If you encounter any bugs please let me know and ill fix them right away, you can reach me through my <a href='https://www.reddit.com/user/Zeeka-' target='_blank'>reddit</a> or on discord, my username is zeeka32</div>
                    <div className='changes'>- Finally this calculator is not official and is not affiliated with Hoyoverse</div>
                    <div className='date'>FAQ</div>
                    <div className='changes'>- Where are the traces ?</div>
                    <div className='changes'>traces are included with the levels, which means at level 60 for example, A2, A4 and the minor traces unlocked at that level will be included automatically.</div>
                    <div className='changes'>- I changed my charcter's level/eidolon but the buffs didn't appear.</div>
                    <div className='changes'>If a buff doesn't have a condition to activate it will generally be added automatically when you reach that eidolon/trace.</div>
                </DialogContent>
            </Dialog>
        </div>
    )
}

export default Intro;